*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.App{
    width: 100%;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    background: linear-gradient(to right, skyblue , pink , skyblue);
}

.Food{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 40px;
}

.Food_box{
    width: 320px;
    overflow: hidden;
    max-height: 599px;
    padding: 10px;
    background: white;
    box-shadow: rgba(100,100,111,0.2)0px 7px 29px 0px;
}

.Food_box img{
    width: 100%;
    height: 235px;
    object-fit:cover;
}

.descritpion h2{
    text-align: center;
    font-size: 40px;
    color: red;
}

.Price{
    margin: 1px 0px;
    font-size: 30px;
    color: #222;
    font-weight: bold;
}

span {
    font-size: 30px;
    text-decoration: line-through;
    margin-left: 10px;
    color: #ccc;
}

.descritpion .Para{
    font-size: 30px;
    margin-bottom: 20px;
    text-align: center;
    color: orange;
}

.descritpion .btn{
    text-align: center;
    margin-top: 20px;
    outline: none;
    border: 1px solid rgb(123, 52, 52);
    padding: 12px 32px;
    border-radius: 40px;
    background: none;
    color:rgb(123, 52, 52);
    font-weight: bold;
    text-transform: uppercase;
    cursor: pointer;
    transition: .3s linear;
}

.descritpion .btn :hover{
    text-align: center;
    background: rgb(123, 52, 52);
    color: #fff;
}
